import React, {Component} from 'react';
import { Card, CardImg, CardImgOverlay, CardFooter, Breadcrumb, BreadcrumbItem, Button, FormGroup, Label, Collapse, Input } from 'reactstrap';
import {Link} from 'react-router-dom';
import {LocalForm} from 'react-redux-form';
import {FadeTransform} from 'react-animation-components';
import {Loading} from './Loading';

function RenderModels({model, onClick, deleteModel}) {
    return(
        <FadeTransform in 
            transformProms={{
                exitTransform: 'scale(0.5) translateY(-50%)' 
            }}>
            <Card style={{backgroundColor:"#000"}}>
                <Link to={`/models/${model.name}`} >
                    <CardImgOverlay><Button className="delete" outline onClick={()=>deleteModel(model._id)}><span className="fa fa-trash" /></Button></CardImgOverlay>
                    <CardImg src={model.image} alt={model.name} />
                    <CardFooter>{model.name.toUpperCase()}</CardFooter>
                </Link> 
            </Card>
        </FadeTransform>
    );
}

class Menu extends Component {
    constructor(props) {
        super(props);

        this.state = {
            models: this.props.models,
            isModelOpen: false
        }
        this.toggleModel = this.toggleModel.bind(this);
        this.handleModelSubmit = this.handleModelSubmit.bind(this);
        this.search = this.search.bind(this);
    }

    toggleModel() {
        this.setState ({
            isModelOpen: !this.state.isModelOpen
        });
    }

    handleModelSubmit(event) {
        //this.toggleModelModal();
        console.log(JSON.stringify(this.image.value));
        this.props.postModel(this.name.value,this.image.value);
    }

    search(event) {
        let arr = [];
        this.state.models.map((model) => arr.push(model.name));
        console.log(arr);
        console.log(arr.find(name => name===this.name.value.toLowerCase()));
    }

    render() {
        const menu = this.state.models.map((model) => {
            return(
                <div className="col-12 col-sm-3 m-1">
                    <RenderModels model={model} deleteModel={this.props.deleteModel} />
                </div>
            );
        });
        if(this.props.models.isLoading) {
            return(
                <div className="container">
                    <div className="row">
                        <Loading />
                    </div>
                </div>
            )
        }
        else if(this.props.models.errMsg) {
            return(
                <div className="container">
                    <div className="row">
                        <div className="col-12">
                            <h4>{this.props.models.errMsg}</h4>
                        </div>
                    </div>
                </div>
            )
        }
        else {
            return(
                <div className="container">
                    <div className="row">
                        <Breadcrumb>
                            <BreadcrumbItem active>Models</BreadcrumbItem>
                        </Breadcrumb>
                    </div>
                    <div className="row">
                        <div className="col-12 col-sm-4">
                            <h3>Models</h3>
                        </div>
                        <div className="col-12 col-sm offset-sm-4">
                            <Button onClick={this.toggleModel}>Add Models</Button>
                            <Collapse isOpen={this.state.isModelOpen}>
                                <LocalForm onSubmit={(values) => this.handleModelSubmit(values)}>
                                    <FormGroup>
                                        <Label htmlFor="name"><h3>Name:</h3></Label>
                                        <Input type="text" id="name" name="name" innerRef={(input) => this.name = input} />
                                    </FormGroup>
                                    <FormGroup>
                                        <Label htmlFor="image"><h3>Image:</h3></Label>
                                        <Input type="text" id="image" name="image" innerRef={(input) => this.image = input} />
                                    </FormGroup>
                                    <Button type="submit" value="submit" color="info">Submit</Button>
                                </LocalForm>
                            </Collapse>
                        </div>
                    </div>
                    <div className="row">
                        {menu}
                    </div>
                    <div className="row">
                        <h3>Search:</h3>
                        <LocalForm onSubmit={(values=>this.search(values))}>
                            <FormGroup>
                                <Input type="text" name="mname" id="name" innerRef={(input) => this.name = input} />
                            </FormGroup>
                            <Button type="submit" value="submit">Submit</Button>
                        </LocalForm>
                    </div>
                </div>
            );
        }
    }
}

export default Menu;