import React, {Component} from 'react';
import {Nav, NavItem, Navbar, NavbarBrand, NavbarToggler, Collapse, Button} from 'reactstrap';
import {NavLink} from 'react-router-dom';

class Header extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isNavOpen: false,
            isModalOpen: false
        }
        this.toggleNav = this.toggleNav.bind(this);
        this.toggleModal = this.toggleModal.bind(this);
        this.handleLogin = this.handleLogin.bind(this);
        this.handleLogout = this.handleLogout.bind(this);
    }

    toggleNav () {
        this.setState({
            isNavOpen: !this.state.isNavOpen
        });
    }

    toggleModal () {
        this.setState({
            isModalOpen: !this.state.isModalOpen
        });
    }

    handleLogin(event) {
        this.toggleModal();
        this.props.loginUser({username: this.username.value, password: this.password.value})
        event.preventDefault();
    }

    handleLogout() {
        this.props.logoutUser();
    }

    render() {
        return(
            <div>
                {this.props.auth.isAuthenticated?
                <Navbar dark color="primary" expand="sm">
                    <div className="container">
                        <NavbarToggler onClick={this.toggleNav} />
                        <NavbarBrand className="mr-auto" href="/"></NavbarBrand>
                        <Collapse isOpen={this.state.isNavOpen} navbar>
                            <Nav navbar>
                                <NavItem>
                                    <NavLink className="nav-data" to="/models">Models</NavLink>
                                </NavItem>
                                <NavItem>
                                    <NavLink className="nav-data" to="/celebs">Celebs</NavLink>
                                </NavItem>
                                <NavItem>
                                <NavLink className="nav-data" to="/pornstars">Stars</NavLink>
                                </NavItem>
                            </Nav>
                            <Nav className="ml-auto" navbar>
                                <NavItem>
                                        <div className="navbar-text mr-3"><h3>{this.props.auth.user.displayName}</h3></div>
                                        <Button outline color="danger" onClick={this.handleLogout}>
                                            <span className="fa fa-sign-out fa-lg"></span> Logout
                                            {this.props.auth.isFetching ?
                                                <span className="fa fa-spinner fa-pulse fa-fw"></span>
                                                : null
                                            }
                                        </Button>
                                </NavItem>
                            </Nav>
                        </Collapse>
                    </div>
                </Navbar>
                :<Navbar dark color="primary" expand="sm">
                    <div className="container">
                        <Nav navbar>
                            <NavItem>
                                <NavLink className="nav-data" to="#">Trials</NavLink>
                            </NavItem>
                        </Nav>
                    </div>
                </Navbar>}
            </div>
        );
    }
}

export default Header;